body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

div.app{
   display:-webkit-flex;
   display:flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   min-height:100px;
   width:90%;
   margin:0 auto;
   color:white;
   
  }
   
   header{
   -webkit-flex-basis:30vh;
           flex-basis:30vh;
   background-image: url(/First-react-project/static/media/photo1.951b361a.png);
   background-size:cover;
   min-height:120px;
   margin-left:100px;
   margin-right:100px;
   border-radius: 20px;
   border: 5px solid black;
   min-width:580px;
  } 
  
  main{
   display:-webkit-flex;
   display:flex;
   -webkit-flex-grow:1;
           flex-grow:1;
   padding:30px 0;
   min-height:800px;
   margin-left:100px;
   margin-right:100px;
  }
  
  aside{
   position: relative;
   min-width: 270px;
   margin-right:30px;
   border: 5px solid black;
   background-image: url(/First-react-project/static/media/photo1.951b361a.png);
   background-size:cover;
   border-radius:20px;
  }
  section.page{
   -webkit-flex-grow:1;
           flex-grow:1;
   border: 5px solid black;
   background-image: url(/First-react-project/static/media/photo1.951b361a.png);
   background-size:cover;
   border-radius:20px;
   min-width:270px;
  }
  
  footer{
   background-color: black;
   color:white;
   padding: 10px 0;
   text-align:center;
   text-transform: uppercase;
   margin-bottom:50px;
   margin-left:100px;
   margin-right:100px;
   border-radius: 20px;
   background-image: url(/First-react-project/static/media/photo1.951b361a.png);
   background-size: cover;
   border: 5px solid black;
   min-width:580px;
  }
.header p{
   font-size:50px;
   font-family: Arial;
   color: white(36, 102, 252);
   text-shadow:
   -3px -3px 0 black,  
   3px -3px 0 black,
    -3px 3px 0 black,
     3px 3px 0 black;
   font-weight:bold;
   text-align: center; 
   text-transform: uppercase; 
   margin-top:100px;
}
nav.main{
   padding-left:10px;
  }
  .main ul{
   list-style-type: none;
  }
  
  nav.main a{
   display: block;
   text-decoration: none;
   color:white;
   transition: .1s;
   text-transform: uppercase;
   font-weight:bold;
   padding: 23px;
  }
  
  nav.main a:hover{
   -webkit-transform: translateX(.5rem);
           transform: translateX(.5rem);
   text-shadow:
   -2px -2px 0 black,  
   2px -2px 0 black,
    -2px 2px 0 black,
     2px 2px 0 black;
  }
  
  
  nav.main li:nth-last-child(1) a.active{
   border-left:10px solid white;
  }
  nav.main li:nth-last-child(2) a.active{
   border-left:10px solid black;
  }
  nav.main li:nth-last-child(3) a.active{
   border-left:10px solid white;
  }
  nav.main li:nth-last-child(4) a.active{
   border-left: 10px solid black;
  }
  nav.main li:nth-last-child(5) a.active{
   border-left:10px solid white;
  }
